.App {
  text-align: center;
  max-width: 90%;
  margin: 0 auto;
}

.controls {
  width: 100%;
  margin-top: 30%;
  height: 200px;
}

.controls__control {
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  float: left;
  cursor: pointer;
}

.controls__minus {
  background:red;
}

.controls__plus {
  background:green;
}






